import type { Controller, TextPoint } from '@ali/4ever-cangjie';
import InlineCode from '../../mo/models';

export function disableCorrectPointAtInnerEdge(controller: Controller, point: TextPoint) {
  const inline = controller.value.document.getClosestInline(point.key);
  if (inline?.type === InlineCode.TYPE) {
    return point.isAtStartOfNode(inline) || point.isAtEndOfNode(inline);
  }
  return false;
}
